* {
  font-family: "Luckiest Guy", cursive;
  font-weight: 100;
  box-sizing: border-box;
}
::-webkit-scrollbar {
  display: none;
}
input::-webkit-outer-spin-button,  /* Removes arrows */
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type="number"] {
  -moz-appearance: textfield;
}

#linkLight:link {
  text-decoration: none !important;
  color: #000 !important;
  transition: 2s !important;
}
#linkDark:link {
  text-decoration: none !important;
  color: wheat !important;
  transition: 2s !important;
}
body {
  background-color: rgb(65, 63, 63);
  display: block;
}
p {
  font-size: 3vh;
}
a,
a:hover,
a:focus,
a:active {
  text-decoration: none;
  color: black;
  font-size: 3vh;
}
.playground-inside {
  height: auto;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.048);
  border-radius: 25px;
  overflow: hidden;
  overflow-y: scroll;
}
#wc-inside {
  height: auto;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.048);
  border-radius: 25px;
  overflow: hidden;
  overflow-y: scroll;
}
.media-inside {
  height: auto;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.048);
  border-radius: 25px;
  overflow: hidden;
  overflow-y: scroll;
  padding: 3rem;
}
.bodyWrapper {
  opacity: 1;
  width: 100%;
  height: 100vh;
  background-color: rgba(25, 42, 35, 1);
  border-radius: 25px;
  transition: 2s;
}

.moveBox {
  position: absolute;
  margin-top: 10%;
  margin-left: 3%;
  margin-right: 3%;
  width: 20%;
  height: 20%;
  background-color: rgba(0, 0, 0, 0.438);
  border-radius: 25px;
  text-align: center;
  overflow: hidden;
}
.moveButton {
  width: 30%;
  height: 30%;
  margin-top: 3%;
  background-color: rgba(204, 204, 204, 0.315);
  color: black;
  border: none;
  border-radius: 15px;
  box-shadow: 0 1px 10px rgb(204 204 204 / 0.315);
  transition: 2s;
}
.menu {
  position: absolute;
  background-color: rgba(255, 255, 255, 0.048);
  left: 50%;
  transform: translate(-50%, 0);
  width: 80%;
  height: 10%;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  border-bottom-right-radius: 25px;
  border-bottom-left-radius: 25px;
  transition: 2s;
  box-shadow: 1px 1px 75px rgb(0 0 0 / 0.715);
}
/*****************************/
.homeContent {
  position: absolute;
  background-color: rgba(255, 255, 255, 0.048);
  border-radius: 25px;
  top: 50%;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  width: 90%;
  height: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  transition: 2s;
  font-size: 10vh;
  box-shadow: 1px 1px 50px rgb(0 0 0 / 0.715);
}
.mediaContent {
  position: absolute;
  background-color: rgba(255, 255, 255, 0.048);
  border-radius: 25px;
  top: 57%;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  width: 90%;
  height: 70%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  transition: 2s;
  font-size: 10vh;
  box-shadow: 1px 1px 50px rgb(0 0 0 / 0.715);
}
.contactContent {
  position: absolute;
  background-color: rgba(255, 255, 255, 0.048);
  border-radius: 25px;
  top: 50%; /* ----------- */
  left: 50%;
  -ms-transform: translate(
    -50%,
    -50%
  ); /*VERTICAL AND HORIZONTAL CENTERING DIV */
  transform: translate(-50%, -50%); /* ----------- */
  width: auto;
  max-width: 90%;
  min-height: 40%;
  height: auto;
  padding: 6rem;

  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  transition: 2s;
  text-align: center;
  line-height: 3em;
  box-shadow: 1px 1px 50px rgb(0 0 0 / 0.715);
}
@media screen and (max-width: 900px) {
  .contactContent {
    flex-direction: column;
    padding: 3rem;
    white-space: nowrap;
  }
}
.playgroundContent {
  position: absolute;
  background-color: rgba(255, 255, 255, 0.048);
  border-radius: 25px;
  top: 50%;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  width: 90%;
  height: 60%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 125px;
  transition: 2s;
  box-shadow: 1px 1px 50px rgb(0 0 0 / 0.715);
}
.wcContent {
  position: absolute;
  background-color: rgba(255, 255, 255, 0.048);
  border-radius: 25px;
  top: 50%;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  width: 90%;
  height: 50%;
  font-size: 15vh;
  transition: 2s;
  box-shadow: 1px 1px 50px rgb(0 0 0 / 0.715);
  overflow-y: scroll;
  overflow: hidden;
}
.login-container {
  width: 60%;
  height: 30%;
  position: absolute;
  top: 50%;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  justify-content: space-evenly;
  align-items: center;
  border-radius: 25px;
  display: flex;
  flex-direction: column;
  z-index: 10;
  transition: 2s;
  box-shadow: 1px 1px 100px rgb(0 0 0 / 0.315);
}
.signup-container {
  width: 60%;
  height: 30%;
  position: absolute;
  top: 50%;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  justify-content: space-evenly;
  align-items: center;
  border-radius: 25px;
  display: flex;
  flex-direction: column;
  z-index: 10;
  transition: 2s;
  box-shadow: 1px 1px 100px rgb(0 0 0 / 0.315);
}
.change-theme-component {
  position: absolute;
  top: 7.5em;
  right: 1em;
  width: 40vw;
  height: auto;
  color: black;
  border-radius: 25px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: 2s;
  padding: 1em;
  box-shadow: 1px 1px 120px rgb(0 0 0 / 0.815);
}
.weather-component {
  position: absolute;
  top: 7em;
  right: 1em;
  width: auto;
  max-width: 75vw;
  height: auto;
  font-size: 1.2rem;
  line-height: 2rem;
  border-radius: 25px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  align-items: center;
  transition: 2s;
  padding: 1em;
  box-shadow: 1px 1px 120px rgb(0 0 0 / 0.815);
  z-index: 10;
}
#vote-component {
  height: auto;
  margin-top: 1rem;
  margin-bottom: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
#votedusernames {
  display: flex;
  flex-direction: row;
  overflow: hidden;
  overflow-y: scroll;
  width: 100%;
  height: auto;
  justify-content: space-evenly;
}
#votedusernames-names {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: auto;
  font-size: 2vh;
}
#h2-voted {
  text-decoration: underline;
}
@media screen and (max-width: 450px) {
  #votedusernames-names {
    font-size: 1.5vh;
  }
}
/*********************************/
/* DAAAAAAMN */
.footer {
  position: absolute;
  text-align: center;
  left: 1rem;
  right: 1rem;
  height: auto;
  top: 97%;
  padding-top: 0.5rem;
  background-color: gray;
  border-bottom-right-radius: 25px;
  border-bottom-left-radius: 25px;
  background-color: rgba(255, 255, 255, 0.048);
  transition: 2s;
}
#icons {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: 2s;
  height: 100%;
  width: 25%;
  cursor: pointer;
}
#icons-very-right {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: 2s;
  height: 100%;
  width: 25%;
  border-bottom-right-radius: 25px;
  cursor: pointer;
}
#icons-very-left {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: 2s;
  height: 100%;
  width: 25%;
  border-bottom-left-radius: 25px;
  cursor: pointer;
}
#icons-active {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: 2s;
  height: 100%;
  width: 25%;
  background-color: rgba(0, 0, 0, 0.238);
  cursor: pointer;
}
#icons-very-right-active {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.238);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: 2s;
  height: 100%;
  width: 25%;
  border-bottom-right-radius: 25px;
}
#icons-very-left-active {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.238);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: 2s;
  height: 100%;
  width: 25%;
  border-bottom-left-radius: 25px;
}
@media screen and (min-width: 600px) {
}
#theme-button {
  width: 70%;
  height: 3em;
  margin-top: 0.5em;
  margin-bottom: 1em;
  border-radius: 25px;
  border: none;
  cursor: pointer;
  opacity: 0.8;
}
#theme-button:hover {
  opacity: 1;
}
#profile-info {
  overflow-wrap: break-word;
}
#playground-row-div {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  width: 50%;
  margin-bottom: 2rem;
  font-size: 1rem;
}
#playground-column-div {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  width: 70%;
  margin-bottom: 2rem;
  font-size: 1.2rem;
}
#fetch-data-section {
  width: 90vw;
  height: auto;
  min-height: 120px;
  background-color: rgba(0, 0, 0, 0.2);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-size: 3vh;
  padding-top: 1em;
  padding-bottom: 1em;
}
.areYouSure {
  position: absolute;
  z-index: 1;
  top: 7em;
  left: 1em;
  width: auto;
  max-width: 75vw;
  height: auto;
  padding: 2rem;
  background-color: rgb(179, 206, 229);

  border-radius: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.contact-element {
  width: 25%;
  height: 100%;
  border-radius: 25px;
  margin-left: 2.5rem;
  margin-right: 2.5rem;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 10;
}

.rgb-sliders {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  color: wheat;
}
@media (max-width: 1200px) {
  .rgb-sliders {
    flex-direction: column;
    color: wheat;
  }
}

#darkModeSpan {
  transition: 2s;
  cursor: pointer;
}
#toggle-theme {
  transition: 2s;
  cursor: pointer;
}
#weather {
  cursor: pointer;
}
#cv-div {
  position: absolute;
  height: 90vh;
  width: 70vw;
  overflow-y: auto;
  overflow-x: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  top: 50%;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
#cv-button {
  color: black;
  cursor: pointer;
  border: none;
  border-radius: 20px;
  margin: 1rem;
  width: auto;
  height: auto;
  font-size: 2vh;
  transition: 2s;
  background-color: rgba(255, 255, 255, 0.8);
}
#calculator-grid {
  z-index: 1;
  display: grid;
  grid-template-columns: (4, 6rem);
  grid-template-rows: (7rem, auto);
}
/* rgba(220, 20, 60, 0.61); LIGHT*/
/* rgba(107, 13, 32, 0.678); DARK */

@media screen and (max-width: 600px) {
  .menu {
    width: 60% !important;
  }
  #icons,
  #icons-very-right,
  #icons-very-left,
  #icons-active,
  #icons-very-right-active,
  #icons-very-left-active {
    font-size: 0.5em !important;
  }

  .authenticator-div {
    width: 15% !important;
  }
}
@media screen and (max-width: 350px) {
  #vote-component {
    padding-top: 3.5rem !important;
  }
}
